body {
  background-color: #15151a;
  font-family: Helvetica, Arial, sans-serif;
}

h1 {
  color: #fff;
  z-index: 10;
  margin: 0;
  font-size: 40pt;
  font-weight: 100;
  position: relative;
}

figure, figcaption {
  margin: 0;
  padding: 0;
}

figcaption {
  height: 0;
}

.glass {
  height: calc(100vh - 80px);
  width: 100vw;
  z-index: 1;
  position: absolute;
  top: 80px;
  left: 0;
}

@media (min-width: 1024px) {
  .glass {
    height: 100vh;
    top: 0;
  }
}

.Ingredients {
  z-index: 10;
  width: calc(100% - 20px);
  margin-left: 10px;
  margin-right: 10px;
  position: relative;
}

.Ingredients ul {
  max-height: 30vh;
  margin-top: 10px;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  overflow: scroll;
}

.ingredient-options-list__overlay {
  pointer-events: none;
  height: 40px;
  z-index: 11;
  background: linear-gradient(#0000, #15151a);
  margin-top: -39px;
  position: relative;
}

.ingredient-search-label {
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  color: #fff;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

#ingredient-search {
  width: 100%;
  color: #838ca2;
  height: 3em;
  background-color: #15151a;
  border: 1px solid #838ca2;
  border-radius: 6px;
  padding: 0 1em;
  font-size: 1.25em;
}

.Ingredients ul li {
  grid-template-columns: 1fr 8em;
  gap: 1em 10px;
  margin-bottom: 10px;
  display: grid;
}

.Ingredients ul li:last-child {
  margin-bottom: 20px;
}

.Ingredients .ingredient-button, .Ingredients .ingredient-unit-button {
  color: #838ca2;
  height: 3em;
  cursor: pointer;
  background-color: #15151a;
  border: 1px solid #838ca2;
  border-radius: 6px;
  font-size: 1.25em;
}

@media (min-width: 1024px) {
  .Ingredients {
    width: 300px;
    z-index: 10;
    position: absolute;
    right: 20px;
  }

  #ingredient-search {
    height: 2em;
    font-size: 1em;
  }

  .Ingredients ul {
    max-height: 70vh;
  }

  .Ingredients ul li {
    grid-template-columns: 1fr 100px;
    gap: 1em 5px;
    margin-bottom: 5px;
  }

  .Ingredients .ingredient-button, .Ingredients .ingredient-unit-button {
    height: 2em;
    font-size: 1em;
  }
}

.glass-actions {
  width: 100%;
  z-index: 10;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 20px;
}

.glass-actions button {
  color: #838ca2;
  height: 2em;
  cursor: pointer;
  background-color: #15151a;
  border: 2px solid #838ca2;
  border-radius: 6px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 40px;
}

#mix-button {
  min-width: 200px;
